#root {
    margin: auto;
    max-width: 610px;
}

button {
    background-color: transparent;
    border: none;
}

.authorBtn {
    background: #333333;
    padding-block-start: 20px;
    padding-block-end: 30px;
    padding-inline-start: 29px;
    color: white;
    width: -webkit-fill-available;
    text-align: start;
    margin-inline-start: 26px;
    font-size: 18px;
}

.randomBtn-wrapper {
    position: relative;
    padding-block-end: 2rem;
    margin-block-start: 5rem;    
}

.randomBtn {
    position: absolute;
    right: 0;
    top: -20px;
}

p {
    font-weight: 500;
    font-size: 24px;
    margin-block-end: 3rem;
    padding-inline-start: 3rem;
    border-left: 5px solid #F7DF94;
}

li {
    list-style: none;
    font-weight: 500;
    font-size: 24px;
    margin-inline-start: -45px;
    margin-block-end: 3rem;
    padding-inline-start: 3rem;
    border-left: 5px solid #F7DF94;
}

.go-back:hover {
    color: #F7DF94;
    font-size: 24px;
}

a {
    text-decoration: none;
    color: white;
}

small {
    color: #828282;
}